import {useState} from 'react';
function Session25(){

const [name,setname] = useState("");
const [email,setemail] = useState("");
const [phone,setphone] = useState(); 
const [course,setcourse] = useState("");    

return(
        <>
<h1>React Form</h1><br/> 
<form>
    <label>Name:</label>
    <input type="text" value={name} onChange={(e)=>setname(e.target.value)}></input><br/><br/>
    <label>Email:</label>
    <input type="email" value={email} onChange={(e)=>setemail(e.target.value)}></input><br/><br/>
    <label>Phone:</label>
    <input type="number" value={phone} onChange={(e)=>setphone(e.target.value)}></input><br/><br/>
<label for="">Course:</label>
<select value={course} onChange={(e)=>setcourse(e.target.value)}>
<option>None</option>
<option>Web Dev</option>
<option>Python</option>
<option>Java</option>
</select>
</form> <br/>
<h2>User details</h2>  
<h4>Name: {name}</h4>     
<h4>Email: {email}</h4>  
<h4>Phone: {phone}</h4> 
<h4>Course: {course}</h4>    
        </>
    )
}
export default Session25;