import React, { Component } from "react";
class Session36 extends Component{
    constructor(){
        super();
        this.state = {
            error: null,
            isLoaded: false,
            data: [],
        }
    }

    componentDidMount(){
        fetch("https://dog.ceo/api/breeds/image/random")
        .then(response=>response.json())
        .then((response)=>{
            this.setState({
                isLoaded: true,
                data: response.message,
            })
        },(error)=>{
            this.setState({
                isLoaded: true,
                error: error,
            })
        })
    }

    render(){
        if(this.state.error){
            return(
                <>
                <h1>Fetch in Class based component</h1>
                <h2>Error 404! Page not found..</h2>
                </>
            )
        }
        else if(!this.state.isLoaded){
            return(
                <>
                <h1>Fetch in Class based component</h1>
                <h2>Loading...</h2>
                </>
            )
        }
        else{
            return(
                <>
                <h1>Fetch in Class based component</h1>
                <img src={this.state.data} alt=""/>
                </>
            )
        }
    }
}
export default Session36;