import { useState, useEffect } from "react";

function Session38(){

const[error,seterror] = useState(null);
const[isLoaded,setisLoaded] = useState(false);
const[data,setdata] = useState([]);


useEffect(()=>{
    const axios = require('axios');
    axios.get("https://icanhazdadjoke.com/slack")
    .then((response)=>{
        console.log(response)
        setisLoaded(true);
        setdata(response.data.attachments[0].text)
    })
    .catch((error)=>{
setisLoaded(true);
seterror(error)
    })  
},[])
if(error){
    return(
        <>
        <h1>Axios</h1>
        <h2>Error 404! Page not found . . .</h2>
        </>
        ) 
}
else if(!isLoaded){
    return(
        <>
        <h1>Axios</h1>
        <h2>Loading . . . </h2>
        </>
        )
}
else{
    return(
        <>
        <h1>Axios</h1>
        <h2>{data}</h2>
        </>
    )
}
}
export default Session38;